import { MockData } from '@/utils'
export default {
  line(
    data = {
      legendData: [''],
      rows: MockData.array(5),
      columns: MockData.date(5, 'week')
    }
  ) {
    const { legendData, rows, columns } = data
    this.mergeOpt.legend.data = legendData
    this.mergeOpt.xAxis.data = columns
    //  legend  
    //  legend length 
    if (Array.isArray(legendData) && legendData.length === 1) {
      this.mergeOpt.series = [
        Object.assign(
          {
            name: legendData[0],
            data: rows
          },
          this.mergeOpt.series[0]
        )
      ]
    } else {
      //   legend
      const seriesArr = []
      for (var i = 0; i < legendData.length; i++) {
        //  data  name  series 
        seriesArr.push(
          Object.assign(
            {
              name: legendData[i],
              data: rows[i]
            },
            this.mergeOpt.series[0]
          )
        )
      }
      this.mergeOpt.series = seriesArr
    }
    this.render()
  },
  bar(
    data = {
      legendData: [''],
      rows: MockData.array(5),
      columns: MockData.date(5, 'week')
    }
  ) {
    const { legendData, rows, columns } = data
    this.mergeOpt.legend.data = legendData
    this.mergeOpt.xAxis.data = columns
    //  legend  
    //  legend length 
    if (Array.isArray(legendData) && legendData.length === 1) {
      this.mergeOpt.series = [
        Object.assign(
          {
            name: legendData[0],
            data: rows
          },
          this.mergeOpt.series[0]
        )
      ]
    } else {
      //   legend
      const seriesArr = []
      for (var i = 0; i < legendData.length; i++) {
        //  data  name  series 
        seriesArr.push(
          Object.assign(
            {
              name: legendData[i],
              data: rows[i]
            },
            this.mergeOpt.series[0]
          )
        )
      }
      this.mergeOpt.series = seriesArr
    }
    this.render()
  },
  pie(
    data = {
      legendData: ['', '', ''],
      columns: [
        { value: 335, name: '' },
        { value: 310, name: '' },
        { value: 234, name: '' }
      ]
    }
  ) {
    const { legendData, columns } = data
    this.mergeOpt.legend.data = legendData
    //  columns  pie
    if (columns.some(v => Array.isArray(v))) {
      // 
      const series = []
      columns.forEach((v, i) => {
        // this.mergeOpt.series[i] || this.mergeOpt.series[0]
        this.mergeOpt.series[i].data = v
        series.push(this.mergeOpt.series[i])
      })
      this.mergeOpt.series = series
    } else {
      this.mergeOpt.series[0].data = columns
    }
    this.render()
  },
  radar(
    data = {
      legendData: ['', ''],
      columns: [
        {
          value: [4300, 10000, 28000, 35000, 50000, 19000],
          name: ''
        },
        {
          value: [5000, 14000, 28000, 31000, 42000, 21000],
          name: ''
        }
      ],
      rows: [
        { name: 'sales', max: 6500 },
        { name: 'Administration', max: 16000 },
        { name: 'Techology', max: 30000 },
        { name: 'Customer', max: 38000 },
        { name: 'Development', max: 52000 },
        { name: 'Marketing', max: 25000 }
      ]
    }
  ) {
    const { rows, columns, legendData } = data
    this.mergeOpt.legend.data = legendData
    this.mergeOpt.radar.indicator = rows
    console.log('data', data)
    //  columns 
    if (columns.some(v => Array.isArray(v))) {
      // 
      const series = []
      columns.forEach((v, i) => {
        // this.mergeOpt.series[i] || this.mergeOpt.series[0]
        this.mergeOpt.series[i].data = v
        series.push(this.mergeOpt.series[i])
      })
      this.mergeOpt.series = series
    } else {
      this.mergeOpt.series[0].data = columns
    }
    this.render()
  }
}
